import {
  UPDATE_LOADING,
} from 'src/vuex/mutation-types'

const state = {
  loading: false,
}

const mutations = {
  [UPDATE_LOADING](state, status) {
    state.loading = !!status
  },
}

export default {
  state,
  mutations
}



