<template>
  <div>
    <loading page :show="loading" color="brand" size="16px"></loading>
    <!--default slot-->
    <router-view></router-view>
  </div>
</template>

<script>
import store from './vuex/store'
import { Loading } from './ui/components'

export default {
  components: {
    Loading,
  },
  store: store,
  vuex: {
    getters: {
      loading: (state) => state.app.loading,
    }
  },
}
</script>

<style lang="less">
</style>



