<!-- Loading -->
<template>
<div class="loading" :class="{'loading-page': page}" v-show="show">
  <beat :color="getColor" :size="size" :margin="margin" :radius="radius"></beat>
  <p class="loading-text"><slot></slot></p>
</div>
</template>

<script>
import Beat from './shape/beat'
import {color} from '../../style/_vars'

export default {
  components: {
    Beat,
  },
  props: {
    show: {
      type: Boolean,
      default: true,
    },
    page: {
      type: Boolean,
      default: false,
    },
    color: {
      type: String,
      default: 'brand' // 可以为 brand, black, white, gray, red, orange, yellow, green, teal, blue, purple
    },
    size: {
      type: String,
      default: '15px'
    },
    // 间距
    margin: {
      type: String,
      default: '2px'
    },
    radius: {
      type: String,
      default: '100%'
    },
  },
  computed: {
    getColor() {
      return color[this.color]
    }
  }
}
</script>

<style scoped lang="less">
@import "../../style/_vars.less";

.loading {
  padding-top: 30px;
  padding-bottom: 50px;
  text-align: center;
  .loading-text{
    display: block;
    font-size: 10px;
    color: @gray;
    margin-top: 10px;
  }
}
.loading-page {
  position: fixed;
  left: 50%;
  top: 50%;
  bottom: auto;
  transform: translate3d(-50%, -50%, 0);
  z-index: 1000;
}
</style>



